<template>
  <div>
     <div
      style="width:100%;
        height:38px;
        background:rgba(13,67,111,1);
        box-shadow:0px 2px 10px 0px rgba(0, 0, 0, 0.05);
        font-size:14px;
        font-family:Microsoft YaHei;
        font-weight:400;
        color:rgba(255,255,255,1);
        padding-left:24px;
        line-height:38px;"
    >
      通道管理
    </div>
    <Card style="margin:10px 10px 0 10px">
      <!--搜索区域-->
      <div style="display:flex;justify-content:space-between">
        <div style="padding-left:20px;">
          <span style="font-size:14px;color:rgba(255,255,255,1);font-weight:400;">通道关键字：</span>
          <Input 
            placeholder="请输入通道关键字"
            :style="{width:200 * base + 'px'}"
            v-model="channelName">
          </Input>
          <Button type="primary" class="btn-right" @click="seachAll">查询</Button>
        </div>
        <div style="padding-right:50px">
          <Button type="primary"  @click="realTimeMonitoring" :loading="btnLoadingStatus">实时监控</Button>
          <Button @click="cardShow"  class="btn-right" style="background:rgba(13,67,111,1);color:rgba(199,219,244,1);">{{showName}}</Button>
        </div>
      </div>
    </Card>
    <div v-if="webStyle==='table'">
      <Tab ref="tab" @tabEvent="tabEvent"/>   
    </div>
    <div v-if="webStyle==='card'">
      <card ref="card" @cardEvent="cardEvent"/>   
    </div>
  </div>
</template>
<script>
import api from '@/api/api'
import Tab from './components/tab.vue'
import card from './components/card.vue'
import { Alert } from 'element-ui';
  export default {
    components:{
      Tab,
      card
    },
    data(){
      return {
        webStyle:'table',
        showName:'卡片展示',
        channelName:'',
        btnLoadingStatus:false,

        base:1,   // 分辨率自适应 计算后的倍数
        tabHeight:0,  // 分辨率自适应调整：表格的高度
      }
    },
    created(){
      this.webStyle = sessionStorage.getItem('cardStyle')?sessionStorage.getItem('cardStyle'):'table'
      this.$nextTick(()=>{
         if(this.webStyle == 'table'){
           this.showName='卡片展示'
           setTimeout(() => {
             this.$refs.tab.showChannelList(this.channelName)
           }, 200);
        }else{
          this.showName='列表展示',
          setTimeout(() => {
            this.$refs.card.showChannelList(this.channelName)
          }, 200);
        }
      })
    },
    mounted(){
      // this.autoFix();
    },
    methods:{

      /** 子组件向父组件传值：获取tab组件中的base */
      tabEvent(data){
        this.base = data;
      },

      /** 子组件向父组件传值：获取card组件中的base */
      cardEvent(data){
        this.base = data;
      },

      // /** 自适应 */
      // autoFix(){
      //   let vm = this;
      //   this.getDom();
      //   window.onresize = function(){
      //     vm.getDom();
      //   }
      // },

      // /** 获取窗口的宽和高 */
      // getDom(){
      //   let width = window.innerWidth;
      //   let height = window.innerHeight;
      //   this.base = (width)/(1024);
      //   this.tabHeight  = height - 350;
      //   // this.tabHeight = height - 325;  // 表格页面有“过滤搜索条件”card，表格上方无Button，则采用该方式计算。
      //   // this.tabHeight = height - 275;  // 表格页面无 “过滤搜索条件”card,但表格上方有Button，则采用该方式计算。
      //   // this.tabHeight = height - 250;   // 表格页面无 “过滤搜索条件”card,表格上方无Button，则采用该方式计算。
      // },

      seachAll(){
        if(this.webStyle == 'table'){
           this.showName='卡片展示',
           this.$refs.tab.showChannelList(this.channelName)
        }else{
          this.showName='列表展示',
          this.$refs.card.showChannelList(this.channelName)
        }
      },
      //实时监控
      realTimeMonitoring(){
        this.btnLoadingStatus = true
        setTimeout(() => {
          this.$router.push({
            name:'channelMonitor'
          })
          this.btnLoadingStatus = false
        }, 500);
      },
      //card展示
      cardShow(){
        if(this.webStyle == 'table'){
           this.webStyle ="card"
           sessionStorage.setItem('cardStyle','card')
           this.showName="列表展示"
           setTimeout(() => {
             this.$refs.card.showChannelList(this.channelName)
           }, 200);
        }else{
          this.webStyle = 'table'
          this.showName="卡片展示"
           sessionStorage.setItem('cardStyle','table')
           setTimeout(() => {
             this.$refs.tab.showChannelList(this.channelName)
           }, 200);
        }
      }
    }
  }
</script>


